import React from 'react';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Input from '@vkontakte/vkui/dist/components/Input/Input';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import FixedLayout from '@vkontakte/vkui/dist/components/FixedLayout/FixedLayout';
import Tabs from '@vkontakte/vkui/dist/components/Tabs/Tabs';
import TabsItem from '@vkontakte/vkui/dist/components/TabsItem/TabsItem';
import FormLayoutGroup from '@vkontakte/vkui/dist/components/FormLayoutGroup/FormLayoutGroup';
import FormLayout from '@vkontakte/vkui/dist/components/FormLayout/FormLayout';
import Link from '@vkontakte/vkui/dist/components/Link/Link';
import ModalRoot from '@vkontakte/vkui/dist/components/ModalRoot/ModalRoot';
import ModalCard from '@vkontakte/vkui/dist/components/ModalCard/ModalCard';
import Icon56MoneyTransferOutline from '@vkontakte/icons/dist/56/money_transfer_outline';
import styles from "../styles/custom-styles.sass";
import ListUsers from './ListUsers';
import Shop from './Shop';


const Home = ({ id, go, fetchedUser, tabShop, tabHome, tabTop, activeTab,
	dataUsers, progressOnChange, dispatch, state, story, addGroup, incDec,
	openBase, dataShop, openPay, inventoryOpen, inventory, dataInventory,
	getUser, durkaCoins, shisoids, transfer, targetId, onChangeHandlerId,
	onChangeHandlerAmount, amount, activeModal, openTransfer, snackbar,
	userPlace, rating
}) => {

	const renderUsers = dataUsers.map((item, index) => {
		return (
			<ListUsers
				key={item.id}
				id={index + 1}
				idVk={item.id}
				photoURL={item.photoURL}
				firstName={item.firstName}
				lastName={item.lastName}
				rating={item.rating}
				amount={item.durkaCoins}
			/>
		);
	});

	const renderShop = dataShop.map(item => {
		return (
			<Shop
				key={item.id}
				id={item.id}
				name={item.name}
				price={item.price}
				icon={item.icon}
				openPay={openPay}
			/>
		);
	});

	const renderInventory = dataInventory.map(item => {
		return (
			<Shop
				key={item.id}
				id={item.id}
				name={item.name}
				price={item.price}
				icon={item.icon}
				openPay={openPay}
			/>
		);
	});

	return (
		<Panel id={id}>
			{/* <ModalRoot activeModal={this.state.activeModal}> */}
			<PanelHeader>ДУРКА</PanelHeader>
			<FixedLayout vertical="top">
				<Tabs className="TabsDefaultUser">
					<TabsItem
						onClick={tabTop}
						selected={activeTab === 'top'}
					>
						ТОП
                    </TabsItem>
					<TabsItem
						onClick={tabHome}
						selected={activeTab === 'home'}
					>
						Профиль
                    </TabsItem>
					<TabsItem
						onClick={tabShop}
						selected={activeTab === 'shop'}
					>
						Магазин
                    </TabsItem>
				</Tabs>
			</FixedLayout>

			{activeTab === 'top'
				?
				<Div className="top">
					<Div>Ваше место: {userPlace} Ваш рейтинг {rating}</Div>

					{renderUsers}
				</Div>
				: ''
			}

			{activeTab === 'home'
				?
				<>
					<Div className="main">
						{fetchedUser &&
							<>
								<Cell
									before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200} size={120} /> : null}
								>
								</Cell>
								<Div>
									{`${fetchedUser.first_name} ${fetchedUser.last_name} ${"(Шизоид)"}`}
								</Div>
							</>
						}

						<Div className="flexStart">
							<Div className="divPadding">
								Привлечено шизоидов: {shisoids} 
							</Div>

							<Div className="divPadding">
								DurkaCoins: {durkaCoins} 
							</Div>
						</Div>

						<Button
							mode="commerce"
							size="xl"
							stretched
							onClick={openBase}
						>
							Привлечь шизоидов
                       </Button>

						<Button
							className="buttonAddGroup"
							mode="commerce"
							onClick={addGroup}
							size="xl"
							stretched
						>
							Подписаться на нашу группу
                        </Button>

						<Button
							className="buttonAddGroup"
							mode="commerce"
							onClick={openTransfer}
							size="xl"
							stretched
						>
							Перевести DurkaCoins
                        </Button>


					</Div>

					<Div className="mainInventory">
						{inventory
							?
							<Link
								className="inventory"
								id={id}
								onClick={inventoryOpen}
								mode="tertiary"
							>
								Открыть инвентарь
                            </Link>
							:
							<>
								<Link
									className="inventory"
									id={id}
									onClick={inventoryOpen}
									mode="tertiary"
								>
									Закрыть инвентарь
					            </Link>
								<Div>
									{renderInventory}
								</Div>
							</>
						}
					</Div>



				</>
				: ''
			}



			{activeTab === 'shop'
				?
				<Div className="top">
					{renderShop}
				</Div>
				:
				''
			}


			{snackbar}

			{/* </ModalRoot> */}
		</Panel>
	);
};

export default Home;
