// Modules
import axios from 'axios';
import '@vkontakte/vkui/dist/vkui.css';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import React, { useState, useEffect, useReducer } from 'react';
import Icon28MusicMicOutline from '@vkontakte/icons/dist/28/music_mic_outline';
import ActionSheet from '@vkontakte/vkui/dist/components/ActionSheet/ActionSheet';
import Input from '@vkontakte/vkui/dist/components/Input/Input';
import ActionSheetItem from '@vkontakte/vkui/dist/components/ActionSheetItem/ActionSheetItem';
import ModalRoot from '@vkontakte/vkui/dist/components/ModalRoot/ModalRoot';
import ModalCard from '@vkontakte/vkui/dist/components/ModalCard/ModalCard';
import FormLayout from '@vkontakte/vkui/dist/components/FormLayout/FormLayout';
import FormLayoutGroup from '@vkontakte/vkui/dist/components/FormLayoutGroup/FormLayoutGroup';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Snackbar from '@vkontakte/vkui/dist/components/Snackbar/Snackbar';
import Icon16Done from '@vkontakte/icons/dist/16/done';
import Icon24Favorite from '@vkontakte/icons/dist/24/favorite';


// import Icon28MoneyTransferOutline from '@vkontakte/icons/dist/28/money_transfer_outline';

// Local files
import config from './config';
import Home from './panels/Home';
import shop from './helpers/dataShop';
import Logger from './helpers/logger';
import requestMethod from './helpers/requestMethod';

import io from 'socket.io-client';

const socket = io('https://digital-space74.tk', {
	path: '/durka/socket',
	query: document.location.search.slice(1)
});

// Loggers
const vk = new Logger('VK');
const api = new Logger('API');
const sock = new Logger('socket');

const App = () => {
	let amount = 0;
	let targetId = 0;
	let targetIdShop = 0;
	const navigator = window.navigator.onLine;

	const [popout, setPopout] = useState();
	const [rating, setRating] = useState(0);
	const [dataShop, setDataShop] = useState(shop);
	const [shopInfo, setShopInfo] = useState();

	const [shisoids, setShisoids] = useState(0);
	const [snackbar, setSnackbar] = useState(null);
	const [userPlace, setUserPlace] = useState(0);
	const [fetchedUser, setUser] = useState(null);
	const [dataUsers, setDataUsers] = useState([]);
	const [durkaCoins, setDurkaCoins] = useState(0);
	const [inventory, setInventory] = useState(true);
	const [validLink, setValidLink] = useState(true);
	const [activeTab, setActiveTab] = useState('home');
	const [getPlatform, setGetPlatform] = useState('');
	const [activeModal, setActiveModal] = useState(null);
	const [activePanel, setActivePanel] = useState('home');
	const [paymentStatus, setPaymentStatus] = useState(false);
	const [networkStatus, setNetworkStatus] = useState(false);
	const [dataInventory, setDataInventory] = useState([
		{
			id: 1,
			name: 'Blue пилюля',
			price: 50,
			icon: <Icon28MusicMicOutline fill='blue' />
		}
	]);

	const updateConnection = (navigator) => {
		if (!navigator) {
			setNetworkStatus(true);
		} else {
			setNetworkStatus(false);
		}
	};

	useEffect(() => {
		if (!navigator) {
			setNetworkStatus(true);
			console.log('checkNetworkStatus', navigator);
		} else {
			setNetworkStatus(false);
			console.log('checkNetworkStatus', navigator);
		}
		console.log('dataUsers:', dataUsers);
	}, [navigator]);

	useEffect(() => {
		// хз чо это
		bridge.subscribe(({ detail: { type, data } }) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});

		// Работа с SOCKET
		// Сразу после запуска
		socket.on('user', (user) => {
			sock.log('user:', user);
			setDurkaCoins(user.durkaCoins);
			setShisoids(user.shisoids.length);
			setRating(user.rating);
			
			getUsers();
			getHash();
		});

		// Каждые 60 сек.
		socket.on('users', setDataUsers);
		socket.on('userPlace', setUserPlace);

		// При новом платеже (входящем или исходящем)
		socket.on('payment', (payment) => {
			const { type, durkaCoins, amount, senderId, targetId, senderName } = payment;
			
			sock.log('payment', payment);
			setDurkaCoins(durkaCoins);

			if (type === 'out') {
				openAlertPay('Перевод успешно выполнен!');
			} else if (type === 'in') {
				openAlertPay(
					<span>
						<a href={`https://vk.com/id${senderId}`} target="_blank">{senderName}</a>
						{` отправил Вам ${amount} DurkaCoins`}
					</span>
				);
			}
		});


		// Работа с ВК
		bridge.send('VKWebAppGetUserInfo').then(setUser);

		bridge.sendPromise('VKWebAppGetClientVersion').then((result) => {
			if (result.platform === 'ios') {
				setGetPlatform('ios');
			}
		}).catch(vk.log);
	}, []);

	function story() {
		const url = `https://vk.com/app7351258#${fetchedUser.id}`;

		bridge.sendPromise('VKWebAppShowStoryBox', {
			background_type: 'image',
			url: 'https://sun1-89.userapi.com/HoZMa0ZNNFflt4SDnoomx0bNiCmRV-bOhbz3qw/hg7IHG1XIvg.jpg',
			attachment: {
				text: 'game',
				type: 'url',
				url
			}
		}).then(res => {
			if(res.data.result === true) {
				axios.post(`${requestMethod}/story`, {
					search: document.location.search,
				});
			}
		}).catch(error => {
			vk.log('story error:', error);
		});
	}

	function addGroup() {
		bridge.send('VKWebAppJoinGroup', { 'group_id': 192779261 });
		vk.log('вызвали функцию.:', addGroup);
	}

	function inventoryOpen() {
		setInventory(!inventory);
	}

	function shopOpenInfo() {
		setShopInfo(!shopInfo);
	}


	function tabShop() {
		setActiveTab('shop');
	}

	function tabHome() {
		setActiveTab('home');
	}

	function tabTop() {
		setActiveTab('top');
	}

	function share(e) {
		//  ДОБАВЛЕНИЕ РЕПОСТА НА СТЕНУ ПОЛЬЗОВАТЕЛЯ
		e.preventDefault();
		const url = `https://vk.com/app7351258#${fetchedUser.id}`;

		bridge.send('VKWebAppShowWallPostBox', {
			message: `А ты уже был в ДУРКЕ? Приложение - ${url}`,
			attachments: `photo-192779261_457239018,https://vk.com/app7351258#${fetchedUser.id}`,
		});
	}

	function getUsers() {
		axios.post(`${requestMethod}/getUsers`, {
			search: document.location.search
		}).then(({ data: { message }}) => {
			api.log('users', message);
			setDataUsers(message);
		});
	}

	// Копирование в буфер
	function copyLink() {
		const link = `https://vk.com/app7351258#${fetchedUser.id}`;
		bridge.sendPromise('VKWebAppGetClientVersion').then((result) => {
			vk.log(result.platform);
			if (result.platform === 'web' || result.platform === 'mobile-web') {
				window.navigator.clipboard.writeText(link).
					then(() => {
						vk.log('successfully set', link);
					}, () => {
						vk.log('write failed', link);
					});
			} else {
				bridge.send('VKWebAppCopyText', { text: link });
			}
		}).catch(error => {
			vk.log(error);
		});
	}

	function openBase() {
		console.log('вызвали функцию', openBase);
		setPopout(<>
			<ActionSheet onClose={() => setPopout(null)}>
				<ActionSheetItem autoclose onClick={copyLink} >
					Скопировать ссылку
				</ActionSheetItem>
				<ActionSheetItem autoclose onClick={story}>
					Поделиться в истории
				</ActionSheetItem>
				<ActionSheetItem autoclose onClick={share}>
					Поделиться на стене
				</ActionSheetItem>
			</ActionSheet>
		</>);
	}

	function getHash() {
		const { hash } = window.location;

		if (
			!hash
			|| Number.isNaN(+(hash.replace('#', '')))
		) {
			return;
		}

		const referrerId = Number(hash.replace('#', ''));

		axios.post(`${requestMethod}/addShisoid`, {
			referrerId: referrerId,
			search: document.location.search
		}).then((res) => {
			api.log('addShisoid', res.data);
		}).catch((error) => {
			api.log('addShisoid', error);
		});
	}

	function pay() { }
	document.qiwiPay = () => { }

	function openTransfer() {
		setActiveModal(
			<ModalRoot activeModal='transfer'>
				<ModalCard
					id='transfer'
					onClose={() => setActiveModal(null)}
					icon={<Avatar size={72} src="https://sun9-50.userapi.com/c857036/v857036390/aa9a0/i-iQZ4973FQ.jpg" />}
					header="Перевести DurkaCoins"
					caption={
						<>
							<FormLayout>
								{/* <FormLayoutGroup top="Введите ссылку на получателя"> */}
								<Input
									top="Введите ссылку на получателя"
									placeholder="https://vk.com/durkacoin"
									// status={validLink ? 'valid' : 'error'}
									// bottom={validLink ? 'Ссылка введена верно!' : 'Ссылка введена неверно'}
									onChange={onChangeHandlerId}
									// value={targetId} 
									type="text" />
								{/* </FormLayoutGroup> */}
								<FormLayoutGroup top="Укажите кол-во DurkaCoins">
									<Input onChange={onChangeHandlerAmount}
									placeholder="10"
										// value={amount} 
										type="text" />
								</FormLayoutGroup>
							</FormLayout>

						</>
					}
					actions={[{
						title: 'Перевести',
						mode: 'commerce',
						action: () => {
							transfer();
						}
					}]}
				>
				</ModalCard>
			</ModalRoot>

		);

	}

	function openPay(e) {
		// setTargetId(e.currentTarget.id);
		targetIdShop = e.currentTarget.id;
		console.log('VALUE targetIdShop', targetIdShop);
		const priceItem = dataShop[targetIdShop - 1].price * 0.2;
		console.log('priceItem1', priceItem);

		setPopout(<>
			<ActionSheet onClose={() => setPopout(null)}>
				<ActionSheetItem autoclose>
					Оплатить DurkaCoin
                </ActionSheetItem>
				<ActionSheetItem autoclose onClick={document.qiwiPay} >
					Оплатить через QIWI
                </ActionSheetItem>
				<ActionSheetItem autoclose onClick={pay}>
					Оплатить через VK Pay
                </ActionSheetItem>
			</ActionSheet>
		</>)
			;
	}

	function onChangeHandlerId(e) {
		const value = e.target.value.trim();
		targetId = value;
	}

	function onChangeHandlerAmount(e) {
		const value = e.target.value.trim();
		amount = +value;

	}

	function transfer() {
		axios.post(`${requestMethod}/sendDurkaCoins`, {
			search: document.location.search,
			targetId,
			amount 
		}).then(({ data: { status, message }}) => {
			api.log('sendDurkaCoins', { status, message });

			if (status === 'success') {
				getUsers();
				amount = 0;
				targetId = 0;
				setActiveModal(null);
			}
		}).catch((err) => {
			api.log('transfer', err);
		});
	}

	function openAlertPay(text) {
		if (snackbar) return;
		setSnackbar(
			<Snackbar
				layout="vertical"
				onClose={() => setSnackbar(null)}
				before={
					<Avatar size={24} style={{backgroundColor:'green'}}>
						<Icon16Done fill="#fff" width={14} height={14} />
					</Avatar>}
			>
				{text}
            </Snackbar>
		);
	}


	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	return (
		<View
			activePanel={activePanel}
			popout={popout}
			modal={activeModal}
		>
			<Home id='home'
				fetchedUser={fetchedUser}
				go={go}
				tabShop={tabShop}
				tabHome={tabHome}
				tabTop={tabTop}
				activeTab={activeTab}
				dataUsers={dataUsers}
				getPlatform={getPlatform}
				story={story}
				addGroup={addGroup}
				openBase={openBase}
				dataShop={dataShop}
				openPay={openPay}
				inventoryOpen={inventoryOpen}
				inventory={inventory}
				dataInventory={dataInventory}
				transfer={transfer}
				targetId={targetId}
				targetIdShop={targetIdShop}
				onChangeHandlerId={onChangeHandlerId}
				onChangeHandlerAmount={onChangeHandlerAmount}
				shisoids={shisoids}
				durkaCoins={durkaCoins}
				amount={amount}
				openTransfer={openTransfer}
				snackbar={snackbar}
				userPlace={userPlace}
				rating={rating}
				shopInfo={shopInfo}
			/>

		</View>
	);
}

export default App;

